@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto-Black.woff2") format('woff2'), url("../fonts/Roboto-Black.woff") format('woff');
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto-Regular.woff2") format('woff2'), url("../fonts/Roboto-Regular.woff") format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto-Bold.woff2") format('woff2'), url("../fonts/Roboto-Bold.woff") format('woff');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto-Medium.woff2") format('woff2'), url("../fonts/Roboto-Medium.woff") format('woff');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
* {
  margin: 0;
  padding: 0;
  border: none;
  -webkit-tap-highlight-color: transparent;
}
*,
:after,
:before {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
ul {
  list-style-type: none;
  -webkit-padding-start: 0;
  padding-inline-start: 0;
}
input::-ms-clear {
  display: none;
}
a,
button {
  display: inline-block;
  box-sizing: border-box;
  padding: 0;
  color: inherit;
  font: inherit;
  text-decoration: none;
  text-align: left;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
:root {
  --font-page: "Roboto", sans-serif;
  --min-width: 375px;
  --max-width: 1920px;
  --sm-width: 576px;
  --md-width: 768px;
  --lg-width: 992px;
  --xl-width: 1280px;
  --xxl-width: 1800px;
  --sm-padding: 20px;
  --md-padding: 30px;
  --xl-padding: 153px;
  --xxl-padding: 300px;
  --mobile-width: 430px;
  --tablet-width: 720px;
  --color-white: #ffffff;
  --color-white-050: #ffffff80;
  --color-white-e3: #ffffffe3;
  --color-white-99: #ffffff99;
  --color-white-b5: #ffffffb5;
  --color-black: #000000;
  --color-black-01: #1E0801;
  --color-black-02: #120501;
  --color-black-03: #1d1a14;
  --color-grey: #f4f4f4;
  --color-gold: #dda046;
  --color-font-light: var(--color-white);
  --color-font-extra-light: var(--color-white-050);
  --color-font-dark: var(--color-black-01);
  --color-font-extra-dark: var(--color-black);
  --color-back-light: var(--color-grey);
  --color-back-extra-light: var(--color-white);
  --color-back-extra-dark: var(--color-black);
  --color-back-dark-01: var(--color-black-01);
  --color-back-dark-02: var(--color-black-02);
  --color-back-dark-03: var(--color-black-03);
  --color-accent: var(--color-gold);
}
.wrapper-width {
  width: 100%;
}
@media (min-width: 1280px) {
  .wrapper-width {
    max-width: 1920px;
    margin-right: auto;
    margin-left: auto;
  }
}
.wrapper-padding {
  padding-right: var(--sm-padding);
  padding-left: var(--sm-padding);
}
@media (min-width: 768px) {
  .wrapper-padding {
    padding-right: var(--md-padding);
    padding-left: var(--md-padding);
  }
}
@media (min-width: 1280px) {
  .wrapper-padding {
    padding-right: var(--xl-padding);
    padding-left: var(--xl-padding);
  }
}
@media (min-width: 1800px) {
  .wrapper-padding {
    margin-right: auto;
    margin-left: auto;
    padding-right: var(--xxl-padding);
    padding-left: var(--xxl-padding);
  }
}
.wrapper {
  width: 100%;
  padding-right: var(--sm-padding);
  padding-left: var(--sm-padding);
}
@media (min-width: 1280px) {
  .wrapper {
    max-width: 1920px;
    margin-right: auto;
    margin-left: auto;
  }
}
@media (min-width: 768px) {
  .wrapper {
    padding-right: var(--md-padding);
    padding-left: var(--md-padding);
  }
}
@media (min-width: 1280px) {
  .wrapper {
    padding-right: var(--xl-padding);
    padding-left: var(--xl-padding);
  }
}
@media (min-width: 1800px) {
  .wrapper {
    margin-right: auto;
    margin-left: auto;
    padding-right: var(--xxl-padding);
    padding-left: var(--xxl-padding);
  }
}
.page {
  min-width: 375px;
  height: 100%;
  margin: 0 auto;
  font-family: var(--font-page);
  font-size: 16px;
  line-height: 117.2%;
  font-weight: 400;
  -webkit-text-size-adjust: 100%;
}
.page b {
  font-weight: 700;
}
.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
  color: var(--color-font-dark);
  background-color: var(--color-back-light);
}
.page__main {
  flex-grow: 1;
}
.blur {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  background-color: transparent;
  background-color: #292929b2;
  opacity: 0;
  pointer-events: none;
}
.blur--active {
  opacity: 1;
  pointer-events: auto;
}
.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  display: flex;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  pointer-events: none;
  visibility: hidden;
}
.modal--left {
  display: block;
}
.modal:focus {
  outline: none;
}
.modal::-webkit-scrollbar-button {
  background-color: transparent;
}
.modal--opened {
  pointer-events: auto;
  visibility: visible;
  -webkit-overflow-scrolling: touch;
}
.modal__wrapper {
  position: relative;
  margin: auto;
  transition: transform 0.5s, opacity 0.5s;
  transform: translateY(100%);
  opacity: 0;
}
.modal--left .modal__wrapper {
  margin: 0;
  transform: translateX(-100%);
}
.modal--opened .modal__wrapper {
  transform: translateX(0) translateY(0);
  opacity: 1;
}
.title {
  font-size: 30px;
  line-height: 117%;
  font-weight: 400;
  text-align: center;
}
@media (min-width: 768px) {
  .title {
    font-size: 48px;
  }
}
@media (min-width: 1800px) {
  .title {
    max-width: 1100px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 96px;
  }
}
.messenger-link {
  position: relative;
  display: flex;
  align-items: center;
}
@media (min-width: 768px) {
  .messenger-link {
    align-items: center;
    column-gap: 4px;
  }
}
@media (min-width: 1280px) {
  .messenger-link {
    justify-content: center;
    column-gap: 15px;
    width: 140px;
    min-height: 46px;
    background-color: #ffffff38;
  }
}
.messenger-link--full {
  justify-content: center;
  column-gap: 15px;
  width: 140px;
  min-height: 46px;
  background-color: var(--color-white);
}
.messenger-link__icon {
  flex-shrink: 0;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  border-radius: 50%;
}
.messenger-link__icon::before {
  position: absolute;
  inset: 0;
  content: "";
  background-repeat: no-repeat;
  background-position: center;
}
.messenger-link--viber .messenger-link__icon {
  background: #7C509A;
}
.messenger-link--viber .messenger-link__icon::before {
  background-image: url("../../images/icons/viber.svg");
  background-size: 18px 19px;
  background-position: top 8px center;
}
.messenger-link--telegram .messenger-link__icon {
  background-color: #039BE5;
}
.messenger-link--telegram .messenger-link__icon::before {
  background-image: url("../../images/icons/telegram.svg");
  background-size: 19px 15px;
}
.messenger-link__name {
  display: none;
}
@media (min-width: 768px) {
  .messenger-link__name {
    display: block;
    font-size: 14px;
  }
}
.messenger-link--full .messenger-link__name {
  display: block;
  text-shadow: 0 3px 4px #00000040;
}
.phone {
  position: relative;
  padding-left: 24px;
}
.phone::before {
  position: absolute;
  inset: 0;
  content: "";
  width: 19px;
  height: 19px;
  background-color: var(--color-accent);
  mask-image: url("../../images/icons/phone.svg");
  mask-size: cover;
  mask-repeat: no-repeat;
  mask-position: center;
}
@media (min-width: 768px) {
  .phone {
    padding-left: 28px;
  }
}
.app-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 200px;
  min-height: 46px;
  color: var(--color-font-extra-dark);
  background: linear-gradient(180deg, #ffdc83 0%, #fbd26a 25%, #c17024 84.9%, #e3a548 100%);
  box-shadow: 0 0 48px 0 #f0c05c8a;
}
.contacts-link {
  position: relative;
  padding-left: 39px;
}
.contacts-link::before {
  position: absolute;
  inset: 0;
  content: "";
  width: 20px;
  height: 20px;
  background-color: var(--color-accent);
  mask-size: cover;
  mask-repeat: no-repeat;
  mask-position: center;
}
.contacts-link--adress::before {
  top: 4px;
  mask-image: url("../../images/icons/map.svg");
}
.contacts-link--email::before {
  mask-image: url("../../images/icons/message.svg");
}
.contacts-link--phone::before {
  top: 7px;
  mask-image: url("../../images/icons/phone.svg");
}
.contacts-link__name {
  display: none;
}
@media (min-width: 1280px) {
  .contacts-link__name {
    display: inline;
  }
}
.site-list {
  display: flex;
  flex-direction: column;
  gap: 27px;
  font-size: 20px;
  line-height: 117%;
  font-weight: 500;
}
@media (min-width: 1800px) {
  .site-list {
    gap: 25px;
    font-size: 24px;
  }
}
.site-list__sublist {
  display: flex;
  flex-direction: column;
  gap: 26px;
  margin-left: 20px;
  margin-top: 29px;
  margin-bottom: 15px;
  font-size: 18px;
  line-height: 117%;
  font-weight: 400;
  opacity: 0.5;
}
@media (min-width: 1800px) {
  .site-list__sublist {
    gap: 24px;
    margin-top: 25px;
    font-size: 24px;
  }
}
.site-list__link:hover,
.site-list__sublink:hover {
  color: var(--color-accent);
}
.site-list--footer {
  display: flex;
  flex-direction: column;
  gap: 15px;
  font-size: 24px;
  line-height: 117%;
  font-weight: 700;
}
.site-list--footer .site-list__sublist {
  display: flex;
  flex-direction: column;
  gap: 11px;
  margin-top: 35px;
  margin-bottom: 10px;
  font-size: 18px;
  font-weight: 400;
  opacity: 1;
}
.social-list {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}
@media (min-width: 768px) {
  .social-list {
    row-gap: 21px;
  }
}
@media (min-width: 1800px) {
  .social-list {
    gap: 48px 38px;
  }
}
.social-list__item {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  gap: 15px;
}
@media (min-width: 1800px) {
  .social-list__item:last-of-type {
    order: -1;
    flex-grow: 1;
  }
}
@media (min-width: 1280px) {
  .social-list__item {
    gap: 25px;
  }
}
.social-list__link {
  position: relative;
  flex-shrink: 0;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.social-list__link::before {
  position: absolute;
  inset: 0;
  content: "";
  background-repeat: no-repeat;
  background-position: center;
}
.social-list__link--facebook {
  background-color: #3b5998;
}
.social-list__link--facebook::before {
  background-image: url("../../images/icons/facebook.svg");
  background-size: 13px 27px;
}
.social-list__link--instagram {
  background: linear-gradient(43.94deg, #eea054 16.16%, #d64763 50.11%, #b62a99 83.68%);
}
.social-list__link--instagram::before {
  background-image: url("../../images/icons/instagram.svg");
  background-size: 17px 17px;
}
.social-list__link--youtube::before {
  background-image: url("../../images/icons/youtube.svg");
  background-size: 40px 40px;
}
.social-list__link-text {
  font-size: 16px;
  line-height: 117%;
  font-weight: 700;
}
@media (min-width: 768px) {
  .social-list__link-text {
    font-size: 18px;
  }
}
@media (min-width: 1800px) {
  .social-list__link-text {
    width: max-content;
  }
}
.navigation-btn {
  position: relative;
  width: 29px;
  height: 24px;
}
.navigation-btn::after {
  position: absolute;
  content: "";
  inset: 0;
  background-image: url("../../images/icons/arrow.svg");
  background-repeat: no-repeat;
  background-size: 29px 24px;
  background-position: center;
  opacity: 1;
  transition: opacity 0.5s ease;
}
.navigation-btn--next::after {
  transform: rotate(180deg);
}
.navigation-btn--disabled::after {
  opacity: 0.2;
  cursor: default;
}
.burger-btn {
  position: relative;
  width: 22px;
  height: 22px;
}
.burger-btn::before {
  position: absolute;
  right: 0;
  transform: translateY(-50%);
  width: 22px;
  height: 2px;
  margin: auto 0;
  background-color: var(--color-font-light);
  box-shadow: 0 calc((-18px) / 2 + 1px) var(--color-font-light), 0 calc(18px / 2 - 1px) var(--color-font-light);
  content: "";
}
@media (min-width: 768px) {
  .burger-btn {
    width: 35px;
    height: 35px;
  }
  .burger-btn::before {
    position: absolute;
    right: 0;
    transform: translateY(-50%);
    width: 35px;
    height: 2px;
    margin: auto 0;
    background-color: var(--color-font-light);
    box-shadow: 0 calc((-28px) / 2 + 1px) var(--color-font-light), 0 calc(28px / 2 - 1px) var(--color-font-light);
    content: "";
  }
}
.site-menu__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 360px;
  min-height: 100%;
  padding: 35px 39px 76px 34px;
  color: var(--color-font-extra-dark);
  background-color: #ffffffde;
  backdrop-filter: blur(16px);
}
@media (min-width: 768px) {
  .site-menu__wrapper {
    width: 503px;
    padding: 35px 33px 76px 88px;
  }
}
@media (min-width: 1280px) {
  .site-menu__wrapper {
    padding: 35px 33px 95px 88px;
  }
}
.site-menu__close-btn {
  position: relative;
  position: absolute;
  top: 25px;
  left: 25px;
  width: 40px;
  height: 40px;
}
.site-menu__close-btn::before,
.site-menu__close-btn::after {
  position: absolute;
  content: "";
  top: calc((100% - 34px) / 2);
  left: calc((100% - 4px) / 2);
  width: 4px;
  height: 34px;
  margin: 0 auto;
  background-color: currentColor;
}
.site-menu__close-btn::before {
  transform: rotate(45deg);
}
.site-menu__close-btn::after {
  transform: rotate(-45deg);
}
.site-menu__logo {
  align-self: end;
  width: 173px;
  height: 45px;
  margin-bottom: 18px;
}
@media (min-width: 768px) {
  .site-menu__logo {
    margin-bottom: 68px;
  }
}
@media (min-width: 1800px) {
  .site-menu__logo {
    margin-bottom: 115px;
  }
}
.site-menu__connection {
  align-self: end;
  margin-bottom: 39px;
}
@media (min-width: 768px) {
  .site-menu__connection {
    display: none;
  }
}
.site-menu__phone {
  margin-bottom: 7px;
  font-size: 18px;
  line-height: 117%;
  font-weight: 700;
}
.main-nav {
