}
.main-nav__call-btn {
  display: none;
}
@media (min-width: 768px) {
  .main-nav__call-btn {
    display: flex;
  }
}
@media (min-width: 768px) {
  .promo {
    display: flex;
    flex-direction: column;
    gap: 15px;
    width: 340px;
    min-height: 452px;
    padding: 55px 12px 45px;
    color: var(--color-font-extra-dark);
    background-color: var(--color-white-b5);
  }
}
@media (min-width: 1800px) {
  .promo {
